@import url(https://fonts.googleapis.com/css2?family=Kalam&family=Permanent+Marker&family=Rock+Salt&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center;padding-bottom:5%}.App .desktop-mobile{display:flex;flex-direction:row;align-items:center;justify-content:center}@media screen and (max-width: 800px){.App .desktop-mobile{display:flex;flex-direction:column}}.App .desktop-mobile .generation{font-family:'Kalam', cursive;font-size:1.8rem;font-weight:bold}h1{color:green;font-family:'Permanent Marker', cursive;font-size:3rem}h2{font-family:'Kalam', cursive;font-size:1.5rem;margin-top:-2%}li{margin:1.5% 0.5%;font-family:'Kalam', cursive;font-size:1.2rem}.button-sec{display:flex;flex-direction:column;align-items:center;justify-content:center;margin:2% 0%;width:100%}@media screen and (max-width: 800px){.button-sec{display:flex;flex-direction:row}}.button-sec button{margin:1.5%;color:white;background-color:green;border:none;border-radius:5px;font-weight:bold;font-family:'Rock Salt', cursive;font-size:0.8rem}.button-sec button:hover{color:green;background-color:white}

